<script>
import uuid from 'uuid';
import SummernoteEditor from '../components/Messages/SummernoteEditor.vue'
import Dropdown from 'bp-vuejs-dropdown';
export default {
  name: "AddMessage",
  components: {
            SummernoteEditor,
            Dropdown
        },
  data() {
    return {
      title: '',
      editor: '',
      author: '',
      height: String,
      width: String,
      window: {
      width: 0,
      height: 0,
      availableHeight: {}
      }
    }

  },
  created() {
    window.addEventListener('resize', this.handleResize)
    this.handleResize();
    this.matchHeight();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    matchHeight () {
      var availableHeight = window.innerHeight - $('.page-container').offset().top - $('.navbar-fixed-bottom').outerHeight() - $('.tabs').outerHeight();
      this.availableHeight = availableHeight
    },
    handleResize() {
      this.window.width = window.innerWidth;
      this.window.height = window.innerHeight;
      this.height = window.innerHeight;
      this.width = window.innerWidth;
    },
    addMessage(e) {
      e.preventDefault();
      const newMessage = {
        id: uuid.v4(),
        author: this.author,
        title: this.title,
        own: true,
        time: "29.01.19",
        completed: false,
        active: false,
        flaged: false,
        body: this.editor
      }
      this.title = '';
      this.author = '';
      this.editor = '';

      this.$emit('add-message', newMessage);

      this.$router.go(-1);
    }
  }
}
</script>

