<script>

    export default {
      props: {
          message: {
              type: Object,
              required: true
          }
      },
        data(){
            return {
              showFields: false,
              availableHeight: {},
              detailsHeight: {}
            }
        },

        methods: {
          toggle(){
            this.showFields = !this.showFields
          },
          matchHeight () {
            var availableHeight = window.innerHeight - $('.page-container').offset().top - $('.navbar-fixed-bottom').outerHeight();
            this.detailsHeight = availableHeight - $('#top-list-height').find('ul').outerHeight(true) - $('.show-message-topbar ').outerHeight() - 4;
          },

          rightClick() {
              $('body').find('input').on('dblclick', function() {
                $(this).prop( "readonly", false );
                $(this).addClass('edit');
              });
          },
          OpenClick() {
              $('.btn-edit-f').on('click', function() {
                $('body').find('input').each(function() {
                  $(this).prop( "readonly", false );
                  $(this).addClass('edit');
                });

              });
          },
          CloseClick() {
              $('.btn-save-f').on('click', function() {
                $('body').find('input').each(function() {
                  $(this).prop( "readonly", true );
                  $(this).removeClass('edit');
                });

              });
          },
        },
        mounted(){
          this.OpenClick();
          this.CloseClick();
          this.rightClick();
          this.matchHeight();
        },

    }
</script>

