<template>
<li class="">
  <div @click="toggle" class="self">
    <span v-if="isFolder" style=" color:grey" class="arrows-for-folders" v-bind:class="{'icon-arrow-up5': open , 'icon-arrow-down5': !open}"></span>
    <span class="icon-folder " style="color: darkgray"></span>
    <span>{{ folder.name }}</span>
  </div>
  <ul class="list-unstyled" v-show="open" v-if="isFolder">
    <FolderItemSearch
      class="item"
      v-for="(folder, index) in folder.children"
      :key="index"
      :folder="folder"
      :title="title">
    </FolderItemSearch>
  </ul>
</li>
</template>

<script>
export default {
  name: "FolderItemSearch",
  props: {
    folder: Object,
    title: ''
  },
  data(){
    return {
      open: true,
    }
  },
  computed: {
    isFolder: function () {
      return this.folder.children &&
        this.folder.children.length
    }
  },
  methods: {
    toggle: function () {
      if (this.isFolder) {
        this.open = !this.open
      }
    },
  }
}
</script>


