<template>
    <modal
      name="AddFolderMain"
      transition="nice-modal-fade"
      :delay="100"
      :width="400"
      :height="200"
      :adaptive="true">
      <div class="box">
    <div class="width-modal">
      <form @submit="addFolder">
  <div class="form-group">
    <label for="folder">Folder Title</label>
    <input type="text" class="form-control" v-model="title" name="title" id="folder" placeholder="write title">
  </div>
  <button type="submit" class="btn btn-primary">Submit</button>
</form>
    </div>
  </div>
  </modal>
</template>
<script>
export default {
  name: 'AddFolderMain',
  data() {
    return {
      title: '',
    }
  },
  props: {
    thisFolder: {}
  },
  methods: {
    addFolder(e) {
    e.preventDefault();
    const newFolder = {
      title: this.title,
      // trash: false,
      folder: this.thisFolder
    }
    // Send up to parent
    this.$emit('add-folder-main', newFolder);

    this.title = '';
  }
  }
}
</script>

<style scoped="">
  .width-modal{
    max-width: 400px;
    padding: 20px;
  }
</style>
