<template>
  <div class="">
  <!-- <div class="w-100 tabs-head">
    <ul class="list-unstyled list-inline m-0 tabs">
      <li v-for="tab in tabs" >{{tab.path}}<router-link :to="tab.path" > <span class="icon-cross"></span> </router-link></li>
      <li>Search <span class="icon-cross"></span></li>
      <li>Search <span class="icon-cross"></span></li>
    </ul>
  </div> -->
  <div v-bind:class="[view2 ? 'grid' : 'grid2']" v-bind:style="{'min-height': availableHeight+'px'}" >

      <div id="custom-sidebar-secondary" class="" style="grid-area: item-1;border-right:1px solid rgb(216, 216, 216); min-width:200px;">
        <Menu
          v-on:add="$emit('add')"
          v-on:add-message="$emit('add-message', $event)"
          v-bind:folders="folders"
          v-bind:tags="tags"
          v-on:del-tag="$emit('del-tag', $event)"
          v-on:del-foldertrash="$emit('del-foldertrash', $event)"
          v-on:del-folder="$emit('del-folder', $event)"
          v-on:add-tag="$emit('add-tag', $event)"
          v-on:add-folder="$emit('add-folder', $event)"
          v-on:add-folder-main="$emit('add-folder-main', $event)">
        </Menu>
      </div>

      <div class="top-bar-list" id="top-list-height" style="grid-area: item-2;">
        <TopMenu
          v-on:add-folder="$emit('add-folder', $event)"
          :tags="tags"
          :folders="folders"
          v-on:tag-message="$emit('tag-message',$event)"
          v-on:remove-tag="$emit('remove-tag')"
          v-on:currentComponent="Component"
          v-on:del-selected="$emit('del-selected')"
          v-on:top-complete="$emit('top-complete')"
          v-on:top-uncomplete="$emit('top-uncomplete')"
          v-on:top-flaged="$emit('top-flaged')"
          v-on:top-unflaged="$emit('top-unflaged')">
        </TopMenu>
      </div>

      <div class="item-3 ui-resizable" style="grid-area: item-3;">
        <Messages
          v-bind:tags="tags"
          v-on:searchdata="$emit('searchdata',$event)"
          v-bind:messages="messages"
          v-on:ShiftClick="$emit('ShiftClick',$event)"
          v-on:OnlyClick="$emit('OnlyClick',$event,visibleDetails = true)"
          v-on:del-message="$emit('del-message',$event)"
          v-on:viewDetails="viewDetails">
        </Messages>
      </div>

      <div class="item-4 ui-resizable" style="grid-area: item-4;">
          <div v-if="visibleDetails">
            <component
            :message="selectedMessage"
            :is="currentComponent">
            </component>
          </div>

          <div v-else >
              <div class="pt-10 text-center">
                <span>click to open message</span>
              </div>
          </div>
      </div>

  </div>
  </div>
</template>

<script>
import Messages from '../components/Messages/Messages';
import MessageDetails from '../components/Messages/MessageDetails';
import MessageConversation from '../components/Messages/MessageConversation';
import AddMessage from './NewMessage';
import TopMenu from '../components/SubTopMenu/TopMenu'
import Menu from '../components/SubMenu/Menu'
// import axios from 'axios';
import _ from 'lodash';


export default {
  name: 'Home',
  components: {
    AddMessage,
    TopMenu,
    Messages,
    Menu,
    MessageDetails,
    MessageConversation,
  },
  props: {
  messages: Array,
  folders: Array,
  tags: Array,
  tabs: Array,
  },
  data() {
    return {
      visibleDetails: false,
      currentComponent: 'MessageDetails',
      selectedMessage: {},
      availableHeight: {},
      view2: true
    }
  },
  mounted () {
    this.matchHeight();
    this.ressizeh();
    this.resshd();
  },
  methods: {
    resshd(){
      $("#custom-sidebar-secondary").resizable({
        handles: 'e',
        // maxWidth: 330,
        // minWidth: 210,
        resize: function (event, ui) {
          var currentWidth = ui.size.width;
          $(this).width(currentWidth);
          // $(this).find('.sidebar-content').width(currentWidth);
        },
        stop: function (event, ui) {
          // sessionStorage.setItem('mailboxSidebarSecondary', ui.size.width);
        }
      });
      $('.item-3').each(function () {
        $(this).resizable({
          handles: 'e',
          // maxWidth: 500,
          // minWidth: 300,
          // alsoResize:'.span-mess',
          // alsoResizeReverse: '.item-4',
          resize: function (event, ui) {
            var currentWidth = ui.size.width;
            $(this).width(currentWidth);
            // $(this).find('.sidebar-content').width(currentWidth);
          },
          stop: function (event, ui) {
            $('grid').find('.item-4').width('auto');
          }
        });
      });
      $('.show-message-grid').each(function () {
        $(this).resizable({
          // maxHeight: 500,
          // minHeight: 150,
          handles: 'n, s',
          resize: function (event, ui) {
            var currentWidth = ui.size.height;
            $(this).height(currentWidth);
            // $(this).find('.sidebar-content').width(currentWidth);
          }
        });
      });
    },
    ressizeh(){
      $( ".dragmessage" ).draggable({
        revert: true ,
      })
      $( ".dropmessage" ).droppable({
      accept: ".dragmessage",
     drop: function( event, ui ) {
      $( this )
       .addClass( "drop-message" );
       ui.draggable.remove();
     },
      });
    },
    matchHeight () {
      var availableHeight = window.innerHeight - $('.page-container').offset().top - $('.navbar-fixed-bottom').outerHeight();
      this.availableHeight = availableHeight;
      // this.showHeight = this.availableHeight - this.$refs.topmenu.clientHeight - this.$refs.search.clientHeight;
      // console.log(this.$refs.search.clientHeight);
      // console.log(this.showHeight);
      // console.log(this.availableHeight) ;
    },
    Component(e){
      this.currentComponent = e;
    },

    viewDetails(id){
        let message = _.find(this.messages, {id:id});
        this.selectedMessage = message;
      },
    },
}
</script>

