<script>
import Dropdown from 'bp-vuejs-dropdown';
import { VueContext } from 'vue-context'
export default {

  name: "MessageItem",
  data() {
    return {
      flaged: false,
      selected: '',
      checked: false,
    }
  },
  components: {
    VueContext,
    Dropdown
  },
  props: {
    iterator: Number,
    tags: Array,
    message: {
            type: Object,
            required: true,
    }
  },
  computed: {
    BackMsg(){
      return this.iterator % 2 !== 0
    },
    messagesbody: function() {
      var n = this.message.body;
      n = n.replace(/(<([^>]+)>)/ig," ");
      return (n);
    },
  },
  methods: {
    hi(){
      alert('hi');
    },
    print: function (event) {
        window.print();
    },
    ShiftClick(){
      var self = this;
      this.$emit('ShiftClick',self.message.id);
      this.message.active = true;
    },
    viewDetailsClicked(){
      this.$emit("viewDetails",this.message.id);
      this.message.completed = true;
    },
    OnlyClick(){
      var self = this;
      this.$emit('OnlyClick',self.message.id);
    },
    markCompleted() {
      this.message.completed = !this.message.completed;
    },
    markFlaged(){
      this.message.flaged = !this.message.flaged;
    },

  }
}
</script>

