<template>

  <div class="max-h" >
    <!-- <div class="side-topbar">
      <div class="pr-1 pl-1 " style=" background: #f5f5f5; position: sticky; z-index: 88; ">
        <div class="input-group">

          <input type="text" v-model="searchdata" id="email-search" class="form-control" style="height: 32px" data-filter-field="email" data-sort-rule="" data-sort-field="" placeholder="Start typing...">
          <span class="input-group-btn add_term">
            <router-link to="/search" ><button id="apply_search" v-on:click="$emit('searchdata',searchdata)" class="btn btn-default btn-xs" type="button">Search</button></router-link>
          </span>
        </div>
      </div>
    </div> -->
    <div class="content-list">
      <div style="border:1px solid rgb(216, 216, 216); border-bottom:none;">
        <div style="background-color: #f3f3f3;">
            <div class="p-1" @click="toggleDate">
              <span>Sorted by date</span>
              <i :class="{ 'icon-arrow-up5': !down, 'icon-arrow-down5': down }"></i>
            </div>
        </div>

        <div id="load-message" v-bind:style="{ 'min-height': loadHeight+'px' }">
        <!-- <div id="load-message"> -->
          <div class="messages" style="background:white">
            <ul  class="DwtListView-Rows">
              <MessageItem
              v-bind:tags='tags'
              v-for="(message,index) in sortedArray"
              :iterator="index"
              v-bind:message="message"
              v-on:ShiftClick="$emit('ShiftClick',$event)"
              v-on:OnlyClick="$emit('OnlyClick',$event)"
              v-on:del-message="$emit('del-message',$event)"
              v-on:viewDetails="$emit('viewDetails',$event)"
              v-on:redirect="$modal.show('Redirect')" >
              </MessageItem>
            </ul>
          </div>
        </div>
        <Redirect>
        </Redirect>
      </div>
    </div>
  </div>
</template>

<script>
import MessageItem from './MessageItem.vue'
import Redirect from './RedirectModal.vue'
export default {

  name: "Messages",
  components: {
    MessageItem,
    Redirect
  },
  data() {
    return {
      sort: 'new',
      searchdata: '',
      availableHeight: {},
      loadHeight: {},
      down: true
    }
  },
  props: {
        tags: Array,
        messages: {},
          },
      computed: {
        sortedArray: function() {
          if (this.sort == 'new') {
            function compare(a, b) {
              if (a.time > b.time)
                return -1;
              if (a.time < b.time)
                return 1;
              return 0;
            }

            return this.messages.sort(compare);
          }
          else if (this.sort == 'name') {
            function compare(a, b) {
              if (a.author < b.author)
                return -1;
              if (a.author > b.author)
                return 1;
              return 0;
            }

            return this.messages.sort(compare);
          }
          else if (this.sort == 'old') {
            function compare(a, b) {
              if (a.time < b.time)
                return -1;
              if (a.time > b.time)
                return 1;
              return 0;
            }

            return this.messages.sort(compare);
          }

        }
    },
    mounted () {
      this.matchHeight();
    },
    methods: {
      toggleDate(){
        this.down = !this.down
      },
      matchHeight () {
        var availableHeight = window.innerHeight - $('.page-container').offset().top - $('.navbar-fixed-bottom').outerHeight();
        this.availableHeight = availableHeight;
        this.loadHeight = availableHeight - $('.top-bar-list').find('ul').outerHeight(true) - $('.side-topbar').outerHeight() - $('.sort').outerHeight() - 8;
        // this.showHeight = this.availableHeight - this.$refs.topmenu.clientHeight - this.$refs.search.clientHeight;
        // console.log(this.$refs.search.clientHeight);
        // console.log(this.showHeight);
        // console.log(this.availableHeight) ;
      },
    }

}
</script>

