<script>
import FolderItem from './FolderItem.vue'
import { VueContext } from 'vue-context'
import AddFolderModal    from '../Folders/AddFolderModal.vue'
import AddFolderModalMain    from '../Folders/AddFolderModalMain.vue'
import ShareFolder    from '../Folders/ShareFolder.vue'
import Dropdown from 'bp-vuejs-dropdown';

export default {
  name: "Folders",
  components: {
    FolderItem,
    VueContext,
    AddFolderModal,
    ShareFolder,
    AddFolderModalMain,
    Dropdown
  },
  props: {
    folders: Array,
  },
  data(){
    return{
      open: true,
      thisFolder: {}
    }
  },
  methods: {
    toggle: function () {
        this.open = !this.open
    },
    Folder(e){
      this.thisFolder = e;
      this.$modal.show('AddFolder')
    },
    FolderShare(e){
      this.thisFolder = e;
      this.$modal.show('ShareFolder')
    }
  }
}
</script>

