<template>
  <modal
  name="AddTag"
  transition="nice-modal-fade"
  :delay="100"
  :width="300"
  :adaptive="true">
  <div class="box">
    <div class="width-modal">
      <form @submit="addTag">
        <div class="form-group">
          <label for="tag">Tag Title</label>
          <input type="text" class="form-control" v-model="title" name="title" id="tag" placeholder="write title">
        </div>
        <div class="form-group">
          <input type="radio"  value="darkblue" v-model="picked">
          <label for="1"><i  class="icon-price-tags" style="color: darkblue;"></i></label>
          <input type="radio"  value="darkred" v-model="picked">
          <label for="2"><i  class="icon-price-tags" style="color: darkred;"></i></label>
          <input type="radio"  value="black" v-model="picked">
          <label for="3"><i  class="icon-price-tags" style="color: black;"></i></label>
          <input type="radio"  value="yellowgreen" v-model="picked">
          <label for="4"><i  class="icon-price-tags" style="color: yellowgreen;"></i></label>
          <input type="radio"  value="red" v-model="picked">
          <label for="5"><i  class="icon-price-tags" style="color: red;"></i></label>
          <input type="radio"  value="purple" v-model="picked">
          <label for="6"><i  class="icon-price-tags" style="color: purple;"></i></label>
          <input type="radio"  value="teal" v-model="picked">
          <label for="7"><i  class="icon-price-tags" style="color: teal;"></i></label>
          <input type="radio"  value="orange" v-model="picked">
          <label for="8"><i  class="icon-price-tags" style="color: orange;"></i></label>
          <input type="radio"  value="lightblue" v-model="picked">
          <label for="9"><i  class="icon-price-tags" style="color: lightblue;"></i></label>
          <input type="radio"  value="grey" v-model="picked">
          <label for="0"><i  class="icon-price-tags" style="color: grey;"></i></label>
        </div>

        <button type="submit" class="btn btn-primary">Submit</button>
      </form>
    </div>
  </div>
</modal>
</template>
<script>
export default {
  name: 'AddTag',
  data() {
    return {
      title: '',
      picked: ''
    }
  },
  methods: {
    addTag(e) {
      e.preventDefault();
      const newTag = {
        title: this.title,
        color: this.picked,

      }
      this.$emit('add-tag', newTag);

      this.title = '';
      this.color = '';
    }
  }
}
</script>

<style scoped="">
.width-modal{
  max-width: 500px;
  padding: 20px;
}
.form-group label{
  padding-left: 5px;
  padding-right: 15px;
}
</style>
