<script>
import { VueContext } from 'vue-context'
import AddFolderModal    from '../Folders/AddFolderModal.vue'
import ShareFolder    from '../Folders/ShareFolder.vue'
import Dropdown from 'bp-vuejs-dropdown';
export default {
  name: "FolderItem",
  components: {
    VueContext,
    AddFolderModal,
    ShareFolder,
    Dropdown
  },
  props: {
    folder: Object,
    title: ''
  },
  data(){
    return {
      open: false,
    }
  },
  computed: {
    isFolder: function () {
      return this.folder.children &&
        this.folder.children.length
    }
  },
  methods: {
    markComplete() {
      this.folder.completed = !this.folder.completed;
    },
    toggle: function () {
      if (this.isFolder) {
        this.open = !this.open
      }
    },
    changeType: function () {
      if (!this.isFolder) {
        this.$set(this.folder, 'children', [])
        this.addChild()
        this.open = true
      }
      else{
        this.addChild()
      }
    },
    addChild: function () {
      this.folder.children.push({
        name: this.title
      })
    }
  }
}
</script>


